import React from 'react'
import {graphql} from "gatsby"

const Article = ({ data }) => {
  return (
    <div>
    {data.datoCmsArticle.title}
    </div>
  )
}

export const query = graphql`
query getArticle ($slug: String) {
  datoCmsArticle(slug: {eq: $slug}) {
    title
    body {
      text
    }
  }
}

`
export default Article
